import React from "react"
import { graphql, Link, useStaticQuery } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const Projects = () => {
  const x = useStaticQuery(
    graphql`
      query names{
        allProjectsJson {
          edges {
            node {
              name
            }
          }
        }
      }
    `
  );

  return (
    <Layout>
      <SEO title="projects" />
      <h1>projects</h1>
      <p>projects</p>
      {JSON.stringify(x)}
    </Layout>
  );
}

export default Projects
